/**
 * 
 * @param enemyName 
 * @param resolvedName enemyDatabase
 */
export function getEnemySkills(enemyName: string, resolvedName?: string): SkillData[] {
  // 
  if (resolvedName && resolvedName in ENEMY_SKILL_MAP) {
    const skillIds = ENEMY_SKILL_MAP[resolvedName];
    return skillIds.map(id => ENEMY_SKILLS[id]).filter(Boolean);
  }
  
  // 
  if (enemyName in ENEMY_SKILL_MAP) {
    const skillIds = ENEMY_SKILL_MAP[enemyName];
    return skillIds.map(id => ENEMY_SKILLS[id]).filter(Boolean);
  }
  
  // 
  // 
  const enemySkillNames = Object.keys(ENEMY_SKILL_MAP).sort((a, b) => b.length - a.length);
  for (const name of enemySkillNames) {
    if (enemyName.includes(name)) {
      console.info(`[] : "${enemyName}"  "${name}"`);
      const skillIds = ENEMY_SKILL_MAP[name];
      return skillIds.map(id => ENEMY_SKILLS[id]).filter(Boolean);
    }
  }
  
  console.warn(`[] : "${enemyName}"`);
  return [];
}

function hashStringToUint32(input: string): number {
  let hash = 2166136261;
  for (let i = 0; i < input.length; i++) {
    hash ^= input.charCodeAt(i);
    hash = Math.imul(hash, 16777619);
  }
  return hash >>> 0;
}

function mulberry32(seed: number) {
  return function () {
    let t = (seed += 0x6d2b79f5);
    t = Math.imul(t ^ (t >>> 15), t | 1);
    t ^= t + Math.imul(t ^ (t >>> 7), t | 61);
    return ((t ^ (t >>> 14)) >>> 0) / 4294967296;
  };
}

function seededPick<T>(items: T[], count: number, seedKey: string): T[] {
  if (count <= 0) return [];
  if (items.length <= count) return [...items];

  const rng = mulberry32(hashStringToUint32(seedKey));
  const indices = items.map((_, i) => i);
  for (let i = indices.length - 1; i > 0; i--) {
    const j = Math.floor(rng() * (i + 1));
    const tmp = indices[i];
    indices[i] = indices[j];
    indices[j] = tmp;
  }
  return indices.slice(0, count).map(i => items[i]);
}

